.clearfix:after,
.clearfix:before {
    content: " ";
    display: table;
    line-height: 0;
}
.clearfix:after {
    clear: both;
}
.clipped {
    border: 0;
    clip: rect(1px, 1px, 1px, 1px);
    height: 1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    white-space: nowrap;
    width: 1px;
}
.clipped--stealth:focus {
    clip: auto;
    height: auto;
    overflow: visible;
    white-space: normal;
    width: auto;
}
.image-stretch {
    height: auto;
    width: 100%;
}
.image-scale {
    height: auto;
    max-width: 100%;
}
.image-center {
    display: table-cell;
    text-align: center;
    vertical-align: middle;
}
.image-center img {
    max-height: 100%;
    max-width: 100%;
}
.image-treatment {
    align-items: center;
    border-radius: 8px;
    display: flex;
    justify-content: center;
    overflow: hidden;
    position: relative;
}
.image-treatment:after {
    background: rgba(0, 0, 0, 0.05);
    bottom: 0;
    content: "";
    display: block;
    left: 0;
    pointer-events: none;
    position: absolute;
    right: 0;
    top: 0;
}
.image-treatment > img {
    display: inline-block;
    max-height: 100%;
    max-width: 100%;
    object-fit: contain;
}
.image-treatment-large {
    align-items: center;
    border-radius: 16px;
    display: flex;
    justify-content: center;
    overflow: hidden;
    position: relative;
}
.image-treatment-large:after {
    background: rgba(0, 0, 0, 0.05);
    bottom: 0;
    content: "";
    display: block;
    left: 0;
    pointer-events: none;
    position: absolute;
    right: 0;
    top: 0;
}
.image-treatment-large > img {
    display: inline-block;
    max-height: 100%;
    max-width: 100%;
    object-fit: contain;
}

.image-disabled {
    filter: var(--color-media-disabled-filter);
}

.text-truncate {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
